import axios from "axios"
import { hex2rgb } from "../../services/vuestic-ui"

const fetchData = () => {
  return axios.get(
    window.location.protocol +
      "//" +
      window.location.host +
      "/api/admin/new_users"
  )
}

export const NewUsersLineChartData = themes => {
  return new Promise(resolve => {
    var generatedData

    fetchData().then(({ data }) => {
      let handledData = {}
      let startRange = data[0]?.eventDate
      let endRange = data[0]?.eventDate

      data.forEach(event => {
        startRange = event.eventDate < startRange ? event.eventDate : startRange
        endRange = event.eventDate > endRange ? event.eventDate : endRange

        let eventDate = new Date(event.eventDate).toLocaleDateString()
        if (!handledData[eventDate]) handledData[eventDate] = 0
        handledData[eventDate]++
      })

      let delta = endRange - startRange
      let dayTimestamp = 86400000
      let daysBetween = delta / dayTimestamp
      let sortedData = {
        [new Date(startRange - dayTimestamp).toLocaleDateString()]: 0
      }

      for (let i = 0; i < daysBetween; i++) {
        let t = dayTimestamp * i + startRange
        let date = new Date(t).toLocaleDateString()
        sortedData[date] = handledData[date] ?? 0
      }

      let dataLength = Object.entries(sortedData).length
      let labels = Object.entries(sortedData).map((entry, i) =>
        i == 0 ? entry[0] : i == dataLength - 1 ? dataLength - 1 : ""
      )
      let events = Object.values(sortedData)

      generatedData = {
        labels,
        datasets: [
          {
            label: "New users",
            background: `linear-gradien(90deg, ${hex2rgb(themes.primary, 0.6).css}, transparent)`,
            borderColor: "transparent",
            data: events
          }
        ]
      }

      resolve(generatedData)
    })
  })
}
